

* {
  transition: all .3s;
  box-sizing: border-box;
}
*::-webkit-scrollbar {
  width: 8px;
  height: 0;
}
*::-webkit-scrollbar-track {
  background: #0003; 
}
*::-webkit-scrollbar-thumb {
  background: #888; 
}
*::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

html, body, #root, #app, #app>div {
  height: 100%;
  padding: 0px;
  margin: 0px;
}

body {
  margin: 0;
  width: 100%;
  height: 100%;
  min-height: 100%;
  font-family: 'Roboto';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root>div {
  min-height: 100%;
  background-color: black;
}

.d-flex {
  display: flex;
}
.flex-wrap {
  flex-wrap: wrap;
}
.justify-content {
  justify-content: center;
}
.justify-content-between {
  justify-content: space-between;
}
.justify-content-center {
  justify-content: center;
}
.justify-content-end {
  justify-content: flex-end;
}
.align-items-center {
  align-items: center;
}

.main-content {
  max-width: 900px;
  margin: auto;
}

.menu {
  position: sticky;
  top: 0;
  z-index: 100;
  padding: 20px;
  background-color: black;
}

.left-menu {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: white;
  display: flex;
  align-items: center;
  padding: 5px;
}
.left-menu-content {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
  border-radius: 11px;
  padding: 1px;
  height: 22px;
}

.left-menu-content>div{
  width: 18px;
  height: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.left-menu-content svg {
  width: 15px;
  height: 15px;
}

.left-menu-content .selected {
  border-radius: 10px;
  background-color: rgb(100, 100, 100);
}

.right-menu {
  display: flex;
  align-items: center;
}

.menu-item {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  margin-left: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: relative;
}
.menu-item::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  border-bottom: 2px solid black;
}
.menu-item.active::after {
  border-color: white;
}

.menu-item:hover svg {
  color: white;
}

.menu-item svg {
  width: 50px;
  height: 50px;
  color: rgb(200, 200, 200);
}
.menu-item.active svg {
  color: white;
  width: 55px;
  height: 55px;
}

.shop-item {
  width: 33%;
  padding: 20px;
  max-width: 300px;
  cursor: pointer;
}

.product-img {
  min-height: 100px;
  max-height: 250px;
  height: 30vw;
  border: 2px solid rgb(66, 133, 245);
  overflow: hidden;
  border-radius: 10px;
}

.product-img:hover img {
  transform: scale(1.1);
}

.shop-item.shared .product-img, .shop-item.purchased .product-img {
  border-width: 15px;
  border-radius: 15px;
}
.shop-item.purchased .product-img {
  border-color: rgb(47 183 89);
}
.shop-item.shared .product-img {
  border-color: rgb(66, 133, 245);
}
.shop-item.shared.purchased .product-img {
  background: linear-gradient(black, black) padding-box, linear-gradient(to bottom, rgb(47 183 89), rgb(66, 133, 245)) border-box;
  border-color: transparent;
}

.item-name {
  color: rgb(66, 133, 245);
  font-weight: bold;
  font-size: 15px;
  margin: 4px 10px 0 0;
}

.insert-link svg, .close-link svg {
  color: rgb(150, 150, 150);
}

.insert-link svg {
  transform: rotate(-45deg);  
}

.insert-link:hover svg, .close-link:hover svg {
  opacity: 0.7;
}

.item-cart, .item-user {
  background-color: rgb(200, 200, 200);
  border-radius: 5px;
  padding: 5px;
  margin-left: 5px;
  cursor: pointer;
}

.more-btn {
  background-color: black !important;
  color: rgb(66, 133, 245) !important;
  font-size: 30px !important;
  font-weight: bold !;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  outline: none !important;
}
.more-btn svg {
  width: 40px;
  height: 40px;
}

.sub-menu {
  padding-right: 10px;
}

.total-price {
  color: #4285f5;
  margin-right: 10px;
  font-size: 30px;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.checkout-btn {
  display: flex;
  align-items: center;
}

.checkout-btn svg {
  height: 40px;
  width: 40px;
}

.card-container {
  position: relative;
}

.link {
  position: absolute;
  top: 15px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #4285f570;
  justify-content: center;
  align-items: center;
  display: none;
}

.link.left {
  left: 15px;
}
.link.right {
  right: 15px;
}

.card-container:hover .link, .selected .link.left {
  display: flex;
}

.link:hover {
  background-color: #4285f550;
}
.selected .link {
  background-color: #4285f5A0;  
}

.link svg {
  color: gray;
  width: 40px;
  height: 40px;
}

.sign-in {
  padding-top: 70px;
  max-width: 400px;
  margin: auto;
}

.input-label {
  color:rgb(66, 133, 245);
  font-size: 25px;
  font-weight: bold;
  text-align: center;
  margin: 10px 0;
}
.input-label.small {
  font-size: 18px;
}
.input-description {
  color:white;
  font-size: 15px;
  text-align: center;
  margin: 10px 0;
  padding: 10px;
  border: 1px solid white;
  border-radius: 10px;
  user-select: none;
}
.input-description a {
  color: white;
}

.input-text input {
  height: 50px;
  width: 100%;
  border: 2px solid #4285f5;
  border-radius: 20px;
  background-color: transparent;
  font-size: 25px;
  font-weight: bold;
  color: white;
  text-align: center;
}

.input-button {
  width: 100%;
  background-color: #4285f5;
  border-radius: 20px;
  color: white;
  font-size: 25px;
  font-weight: bold;
  height: 50px;
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.input-button:hover {
  opacity: 0.8;
}
.input-button:active {
  opacity: 0.6;
}

.error-msg {
  background-color: rgb(218, 134, 8);
  color: white;
  font-size: 15px;
  padding: 0 20px;
  border-radius: 15px;
  margin-top: 20px;
}

.email-header {
  font-size: 30px;
  font-weight: bold;
  color: #4285f5;
  display: flex;
  align-items: center;
}

.profile-page {
  max-width: 400px;
  padding-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
}

.avatar {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 2px solid #4285f5;
  overflow: hidden;
  cursor: pointer;

  display: flex;
  align-items: center;
  justify-content: center;
}
.avatar.selected {
  border-width: 7px;
}

.avatar img {
  width: 180px;
  height: 180px;
  border-radius: 50%;
}

.share-email {
  color: #4285f5;
  font-size: 25px;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.shared-header {
  display: flex;
  justify-content: center;
  color: #4285f5;
  font-size: 25px;
  font-weight: bold;
}

.shared-url {
  text-decoration: underline;
}
.shared-url svg {
  margin-left: 15px;
  color: rgb(150, 150, 150);
  cursor: pointer;
}
.shared-url svg:hover {
  color: rgb(200, 200, 200);
}

.no-card-container {
  padding-top: 50px;
  max-width: 400px;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.no-card-container img {
  width: 200px;
}

.no-card-text {
  margin-top: 20px;
  font-size: 30px;
  font-weight: bold;
  color: #4285f5;
  text-align: center;
}

.privacy-iframe {
  width: 100%;
  height: calc(100vh - 120px);
  border: none;
}

.loading-container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: #00000080;
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
}
.verify-container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: #00000080;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #4285f5;
  font-size: 20pt;
}
.loading-spinner {
  width: 80px !important;
  height: 80px !important;
  color: rgb(66, 133, 245) !important;
}

.purchase-button {
  max-width: 400px;
  margin: auto;
}

@media screen and (max-width: 768px) {
  .menu {
    padding-left: 0;
    padding-right: 0;
  }
  .shop-item {
    width: 50%;
  }
  .product-img {
    height: 50vw;
  }
  .shared-header {
    font-size: 15px;
  }
  .share-email {
    font-size: 15px;
  }
  .shared-url svg {
    width: 15px;
    height: 15px;
    margin-left: 5px;
  }
}
@media screen and (max-width: 500px) {
  .shop-item {
    width: 100%;
  }
  .menu-item {
    margin-left: 0;
  }
  .product-img {
    height: 50vw;
  }
}
@media screen and (max-width: 390px) {
  .shared-header {
    font-size: 13px;
  }
  .share-email {
    font-size: 13px;
  }
}