.quickview-container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  backdrop-filter: blur(3px);
  background-color: #000000A0;
}

.quickview {
  position: relative;
  margin-top: 110px;
  max-height: calc(100vh - 150px);
  width: 700px;
  padding: 0 15px 15px;
  border: 10px solid rgb(66, 133, 245);
  border-radius: 10px;
  background-color: black;
  overflow-y: auto;
}

.quickview::-webkit-scrollbar {
  width: 0;
}

.quickview-content {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  height: 100%;
  width: 100%;
  overflow-y: auto;
}

.left-bar {
  width: 300px;
}

.right-bar {
  width: 1px;
  flex-grow: 1;
  color: rgb(66, 133, 245);
}

.top-nav {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 5px;
  position: sticky;
  top: 0;
  margin-left: auto;
  padding: 5px 0;
  background-color: black;
}
.top-nav-item {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  cursor: pointer;
  color: rgb(66, 133, 245);
}
.top-nav-item.active {
  box-shadow: inset 0 0 9px 4px;
}
.top-nav-item:hover {
  background-color: #ffffff30;
}
.top-nav-item:active {
  background-color: #ffffff40;
}

.logo-container {
  width: 100%;
  max-height: 400px;
  padding: 10px;
  border: 2px solid rgb(66, 133, 245);
  border-radius: 7px;
  display: flex;
  justify-content: center;
}

.logo-container img, .quickview-photo img {
  width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.photo-container {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  margin-top: 10px;
}

.quickview-photo {
  width: 70px;
  height: 80px;
  padding: 3px;
  border: 1px solid rgb(66, 133, 245);
  border-radius: 4px;
  cursor: pointer;
  overflow: hidden;
  display: flex;
  align-items: center;
}

.quickview-photo:hover img {
  scale: 1.2;
}

.quickview-item-link {
  word-break: break-all;
  text-decoration: underline;
  color: #66ceff;
  margin-top: 30px !important;
}

.description-editor, .description-editor * {
  width: 100%;
  color: rgb(66, 133, 245) !important;
}
.description-editor fieldset {
  border-color: rgb(66, 133, 245) !important;
}

.quickview-left-navigator, .quickview-right-navigator {
  position: absolute;
  top: 40vh;
  border-radius: 50%;
  background-color: #1e1e1e;
  color: white;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.quickview-left-navigator {
  left: calc(50vw - 420px);
}
.quickview-right-navigator {
  right: calc(50vw - 420px);
}
.quickview-left-navigator:hover, .quickview-right-navigator:hover {
  opacity: 0.7;
}
.quickview-left-navigator:active, .quickview-right-navigator:active {
  opacity: 0.9;
}

@media screen and (max-width: 768px) {
  .quickview {
    width: 350px;
  }
  .quickview-content {
    margin-top: 5px;
  }
  .quickview-left-navigator {
    left: 0;
  }
  .quickview-right-navigator {
    right: 0;
  }
}